<template>
  <!-- Present category navigation limited to a single store. Used for both grocery and retail. -->
  <div>
    <DeliveryBarWrapper class="delivery-bar-wrapper--mt-5 container">
      <div class="delivery-bar-wrapper__left-group">
        <DeliveryCurrAddressContainer
          :sellerType="currSellerType"
          :noSuccessRedirect="true"
          :storeName="name"
          :customText="currentAddress"
          :supportPickupShipment="supportPickupShipment"
          viewName="ViewProducts"
        />
      </div>
      <MyProductPopupWrapper class="my-product_same-as-last-container">
        <MyProductAndSameAsLastBtns
          :onMyProductClick="toggleMyProducts"
          :merchant="merchant"
          :isMyProductsFiltered="isMyProductsFiltered"
          :storeId="id"
        />
      </MyProductPopupWrapper>
    </DeliveryBarWrapper>

    <section class="container">
      <AisCategoryFilter
        :store="store"
        :baseFilter="filterStoreID"
        :myProductIDs="myProductIDs"
        :isMyProductsFiltered="isMyProductsFiltered"
        :basePath="path"
      >
        <template #customRender>
          <div class="container">
            <FilterAndOptions :merchant="merchant" :storeId="id" />
            <h1 :class="['page-title', !store && 'app-ph']">{{ name }}</h1>
            <h1 v-show="subCategoryName" class="page__title--sub-category">
              {{ subCategoryName }}
            </h1>

            <router-link
              v-if="name && name.includes('Nettó')"
              :to="`/grocery/${path}`"
            >
              <span class="back-to-overview">
                <img
                  alt="back to last page"
                  src="../assets/svg/arrow-left.svg"
                />
                {{ $t("backToOverview") }}
              </span>
            </router-link>
          </div>
        </template>
      </AisCategoryFilter>
    </section>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
import DeliveryCurrAddressContainer from "@/components/DeliveryCurrAddressContainer";
import AisCategoryFilter from "@/components/AisCategoryFilter";
import FilterAndOptions from "@/components/FilterAndOptions";
import DeliveryBarWrapper from "@/components/DeliveryBarWrapper";
import MyProductPopupWrapper from "@/components/MyProductPopupWrapper";
import MyProductAndSameAsLastBtns from "@/components/MyProductAndSameAsLastBtns";

import getPathData from "@/mixins/getPathData";
import idTracker from "@/mixins/idTracker";
import merchant from "@/mixins/merchant";

import get from "@/util/get";
import { SELLER_TYPES, FULFILLMENT_METHOD } from "@/util/constants";
import { MODAL_KEY_MORE_OPTIONS_POP_UP } from "@/store/modalModule";

export default {
  name: "ViewProducts",
  mixins: [getPathData, idTracker, merchant],
  components: {
    DeliveryCurrAddressContainer,
    AisCategoryFilter,
    DeliveryBarWrapper,
    FilterAndOptions,
    MyProductPopupWrapper,
    MyProductAndSameAsLastBtns,
  },
  props: {
    path: String,
    isActiveMyProductButton: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      store: undefined,
      isMyProductsFiltered: false,
      myProductIDs: [],
      currSellerType: undefined,
    };
  },

  methods: {
    ...mapActions([
      "getPickupLocations",
      "setNavBarTitle",
      "setSelectedFilterOption",
      "setFilterOptions",
      "toggleModal",
      "getStoreIdURLMap",
    ]),
    toggleMyProducts() {
      const isLogin = this.$store.state.customer.is_logged_in;
      this.toggleShowMorePopup(false);
      if (!isLogin) {
        this.$store.dispatch("toggleLogin", {
          params: { isActiveMyProductButton: true },
        });
        return;
      }
      !this.myProductIDs.length && this.fetchMyProducts();
      this.isMyProductsFiltered = !this.isMyProductsFiltered;
    },
    toggleShowMorePopup(isVisible) {
      this.toggleModal({
        modalKey: MODAL_KEY_MORE_OPTIONS_POP_UP,
        isVisible,
      });
    },
    async fetchMyProducts() {
      const params = new URLSearchParams();
      params.append("service", "my_products");
      params.append("customer_email", this.$store.state.customer.email);
      params.append("customer_id", this.$store.state.customer.id);
      params.append("store_id", this.id);
      const response = await axios.post("/ws.php", params);
      this.myProductIDs = Object.values(get(response, "data.0", [])).map(
        ({ entity_id }) => entity_id
      );
    },
  },
  computed: {
    ...mapGetters(["getNettoNameByID", "fulfillmentMethod"]),
    name() {
      return this.store && this.store.name;
    },
    id() {
      return this.store && this.store.entity_id;
    },
    filterStoreID() {
      return `stores.store_id: ${this.id}`;
    },
    subCategoryName() {
      const fullPath = get(this.$route, "params.pathMatch");
      if (!fullPath) return "";
      let name = fullPath.split("/")[fullPath.split("/").length - 1];
      return name;
    },
    currentAddress() {
      if (
        this.fulfillmentMethod === FULFILLMENT_METHOD.DELIVERY ||
        !this.supportPickupShipment
      ) {
        return this.currDeliveryText;
      }

      return get(this.merchant, "restaurant_address");
    },
  },
  async mounted() {
    const data = await this.getPathData(`verslun/${this.path}`);
    this.getPickupLocations();
    const dataInArr = Object.values(data);
    this.currSellerType =
      get(dataInArr, `${dataInArr.length - 1}.0.view`) === "Groceries"
        ? SELLER_TYPES.GROCERY
        : SELLER_TYPES.STORE;
    this.store = data[this.currSellerType === SELLER_TYPES.GROCERY ? 4 : 3][0];
    this.setNavBarTitle({
      en: get(this.store, "english_name", ""),
      is: get(this.store, "is_name", ""),
    });

    if (this.isActiveMyProductButton) {
      this.toggleMyProducts();
    }
  },
  async created() {
    await this.getStoreIdURLMap();
  },
  watch: {
    async store() {
      const storeId = this.id;

      const filterOptions = [
        {
          label: this.getNettoNameByID(storeId),
          value: storeId,
        },
        {
          label: "*",
          value: "*",
        },
      ];
      this.setFilterOptions(filterOptions);
    },
  },
};
</script>

