  display: flex;
}

.back-to-overview {
  opacity: 0.5;
  color: black;
}

.back-to-overview img {
  height: 0.8em;
  padding-bottom: 2px;
  padding-right: 5px;
}

.my-products {
  display: flex;
  position: relative;
  align-items: center;
  cursor: pointer;
  padding: 5px;
  margin-right: 20px;
}

.my-products.checked {
  transform: translateY(1px);
  border-bottom: 2px solid black;
  width: 100%;
}

.my-products input {
  cursor: pointer;
  margin: 0;
  appearance: none;
  border: none;
  display: inline-block;
  height: 20px;
  width: 20px;
  background: 50% 50% no-repeat url("../assets/svg/bookmark-line.svg");
  outline: none;
}
.my-products input:checked {
  background: 50% 50% no-repeat url("../assets/svg/bookmark-fill.svg");
}

.page__title--sub-category {
  font-size: 26px;
  display: block;
}

.page-title {
  font-size: 26px;
  display: none;
}

@media screen and (min-width: 768px) {
  .page-title {
    display: block;
  }
  .page__title--sub-category {
    display: none;
  }
  .my-product_same-as-last-container {
    display: flex;
