export default {
  name: "subCategoryCenter",
  data() {
    return {
      chipItemActivetimmerId: null,
    };
  },
  methods: {
    scrollToCenterScreen() {
      if (window.screen.width < 768) {
        clearTimeout(this.chipItemActivetimmerId);
        this.chipItemActivetimmerId = setTimeout(() => {
          const itemSelected = document.getElementsByClassName(
            "category-filter__item--selected"
          );
          itemSelected[0] &&
            itemSelected[0].scrollIntoView({
              behavior: "smooth",
              inline: "center",
              block: "center",
            });
        }, 100);
      }
    },
  },
  mounted() {
    window.addEventListener("resize", this.scrollToCenterScreen.bind(this));
  },
  updated() {
    this.scrollToCenterScreen();
  },
  beforeDestroy() {
    clearTimeout(this.chipItemActivetimmerId);

    window.removeEventListener("resize", this.scrollToCenterScreen.bind(this));
  },
};
