<template>
  <!-- Use instantsearch hierarchicalMenu to implement category navigation within a given store -->
  <div class="row" v-if="!store">
    <div class="category-list__sidebar category-list__sidebar--app-ph col-xs-3">
      <div class="category-filter">
        <div class="app-ph" />
      </div>
    </div>
    <div class="category-list__content col-xs-12 col-sm-9">
      <div class="row">
        <grid-grocery-item
          v-for="(item, index) in [...Array(12)]"
          :key="index"
          v-bind="item"
          class="col-xs-6 col-sm-4 col-md-3"
        />
      </div>
    </div>
  </div>
  <ais-instant-search
    v-else
    :key="remountKey"
    class="row"
    index-name="products"
    :search-client="searchClient"
    :routing="routing"
  >
    <ais-hierarchical-menu
      class="category-filter category-filter--chip-list card category-filter--sub-cat"
      :attributes="attributes"
      :limit="100"
      :class-names="{
        'ais-HierarchicalMenu-item': 'category-filter__item',
      }"
      :show-parent-level="true"
      :root-path="rootPath"
    >
      <div slot-scope="{ items, refine, createURL }">
        <AisHierarchicalMenuChipList
          :items="items"
          :refine="refineAndScroll(refine)"
          :createURL="createURL"
          :name="name"
          :basePath="basePath"
          isRoot
        />
      </div>
    </ais-hierarchical-menu>
    <slot name="customRender" />
    <div
      class="category-list__sidebar col-xs-3"
      ref="categoryFilterModalWrapperRef"
    >
      <BaseModal
        :isVisible="true"
        :clickOutsideToClose="false"
        :disableBodyScroll="false"
        @hide="hideFilterModal"
        class="category-filter-modal"
      >
        <template #header>{{ $t("filter") }}</template>
        <template #footer>
          <div class="btns-container btns-container--center">
            <p class="category-list__count" v-show="counts !== null">
              {{ counts }} {{ $t(counts === 1 ? "itemFound" : "itemsFound") }}
            </p>
            <div class="btn-container-groups">
              <BaseButton
                class="tertiary btn--px-20 btn--mx-15"
                @click.native="resetFilter"
                >{{ $t("resetFilter") }}</BaseButton
              >
              <BaseButton
                class="primary btn--px-20 btn--mx-15"
                @click.native="hideFilterModal"
                >{{ $t("showResults") }}</BaseButton
              >
            </div>
          </div>
        </template>

        <div class="category-filter">
          <div class="accordion" id="filter-accordion">
            <div class="card">
              <div class="card-header" id="headingOne">
                <h5>
                  <!-- TODO fix collapsible button (bootstrap collapsible requires JQuery) OR refactor not to be a button -->
                  <div>{{ $t("categories") }}</div>
                </h5>
              </div>
              <div
                id="collapseOne"
                class="collapse in"
                aria-labelledby="headingOne"
              >
                <div class="card-body">
                  <div class="category-list">
                    <ais-configure
                      :filters="filters"
                      :hits-per-page.camel="24"
                    />
                    <ais-hierarchical-menu
                      class="category-filter card"
                      :attributes="attributes"
                      :limit="100"
                      :class-names="{
                        'ais-HierarchicalMenu-item': 'category-filter__item',
                      }"
                      showParentLevel
                      :root-path="rootPath"
                    >
                      <div
                        slot-scope="{
                          items,
                          canToggleShowMore,
                          isShowingMore,
                          refine,
                          toggleShowMore,
                          createURL,
                        }"
                      >
                        <AisHierarchicalMenuList
                          :items="items"
                          :refine="refineAndScroll(refine)"
                          :createURL="createURL"
                          :name="name"
                          :basePath="basePath"
                          isRoot
                        />
                        <p
                          class="more-item"
                          v-show="canToggleShowMore"
                          @click="toggleShowMore()"
                        >
                          {{
                            isShowingMore ? "- Sjá færri" : "+ Sjá alla flokka"
                          }}
                        </p>
                      </div>
                    </ais-hierarchical-menu>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </BaseModal>
    </div>
    <div class="category-list__content col-xs-12 col-sm-9">
      <AisInfiniteLoading class="row" :transform-items="transformItems">
        <template slot="items" slot-scope="{ items }">
          <!-- <ais-highlight :hit="item" attribute="name" /> -->
          <grid-grocery-item
            class="col-xs-6 col-sm-4 col-md-3"
            v-for="item in items"
            :key="item && item.objectID"
            :entity_id="parseInt(item && item.objectID)"
            :parent_product_id="parseInt(id)"
            v-bind="item"
          />
        </template>
      </AisInfiniteLoading>
    </div>
  </ais-instant-search>
</template>

<i18n>
{
  "is": {
    "showMore": "Sjá fleiri vörur",
    "filter": "Sía",
    "showResults": "Sýna niðurstöður",
    "categories": "Flokkar",
    "resetFilter": "Núllstilla síu"
  },
  "en": {
    "showMore": "Show more items",
    "filter": "Filter",
    "showResults": "Show results",
    "categories": "Categories",
    "resetFilter": "Reset filter"
  }
}
</i18n>

<script>
import algoliasearch from "algoliasearch/lite";
import { singleIndex as singleIndexMapping } from "instantsearch.js/es/lib/stateMappings";

import GridGroceryItem from "@/components/GridGroceryItem";
import AisHierarchicalMenuList from "@/components/AisHierarchicalMenuList";
import AisHierarchicalMenuChipList from "@/components/AisHierarchicalMenuChipList";
import BaseButton from "@/components/BaseButton";
import BaseModal from "@/components/BaseModal";
import AisInfiniteLoading from "@/components/AisInfiniteLoading";
import range from "@/util/range";
import get from "@/util/get";
import { MODAL_KEY_CATEGORIES_FILTER } from "@/store/modalModule";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "AisCategoryFilter",
  components: {
    GridGroceryItem,
    AisHierarchicalMenuList,
    AisHierarchicalMenuChipList,
    BaseButton,
    BaseModal,
    AisInfiniteLoading,
  },
  props: {
    store: Object,
    baseFilter: String,
    attribute: {
      type: String,
      default: "store_categories",
    },
    basePath: {
      type: String,
    },
    levels: {
      type: Number,
      default: 3,
    },
    rootPath: {
      type: String,
      default: "",
    },
    myProductIDs: {
      type: Array,
      default: () => [],
    },
    isMyProductsFiltered: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    // https://www.algolia.com/doc/guides/building-search-ui/going-further/routing-urls/vue/#seo-friendly-urls
    const { basePath, attribute, updateRemountKey } = this;
    const vueRouter = this.$router;
    const router = {
      read() {
        const urlParts = vueRouter.currentRoute.path.split("/").filter(Boolean);
        const urlCategories = urlParts
          .splice(urlParts.indexOf(basePath) + 1, urlParts.length)
          .map(decodeURIComponent);
        const hierarchicalMenu = {
          [`${attribute}.lvl0`]: [this.rootPath, ...urlCategories].filter(
            Boolean
          ),
        };
        return { hierarchicalMenu };
        // return vueRouter.currentRoute.query;
      },
      write(routeState) {
        // console.log("write -> routeState", routeState)
        // vueRouter.replace({
        //   query: routeState,
        // });
      },
      createURL(routeState) {
        const { hierarchicalMenu } = routeState;
        const categories = !hierarchicalMenu
          ? []
          : routeState.hierarchicalMenu[`${attribute}.lvl0`].filter(
              categoryName => categoryName !== this.rootPath
            );
        const urlBase =
          location.pathname.slice(0, location.pathname.indexOf(basePath)) +
          basePath;

        const createdURL = !hierarchicalMenu
          ? location.pathname
          : `${[urlBase, ...categories].join("/")}`;

        return createdURL;
      },
      onUpdate(cb) {
        this._onPopState = ({ state }) => {
          const routeState = state;
          // at initial load, the state is read from the URL without
          // update. Therefore the state object is not there. In this
          // case we fallback and read the URL.
          updateRemountKey();
          if (!routeState) {
            cb(this.read());
          } else {
            cb(routeState);
          }
        };

        window.addEventListener("popstate", this._onPopState);
      },
      dispose() {
        window.removeEventListener("popstate", this._onPopState);
        this.write();
      },
    };
    return {
      searchClient: algoliasearch(
        "5D97PB00YB",
        "6e6550362dda457bf773b121e6e6acf0"
      ),
      routing: {
        router,
        stateMapping: singleIndexMapping("products"),
      },
      remountKey: undefined,
    };
  },
  methods: {
    ...mapActions(["hideModal", "setCounts"]),
    hideFilterModal() {
      this.hideModal(MODAL_KEY_CATEGORIES_FILTER);
    },
    transformItems(items) {
      return items.map(item => ({
        ...item,
        entity_id: +item.objectID,
      }));
    },
    refineAndScroll(refine) {
      return value => {
        setTimeout(() => refine(value), 100);
        window.scrollTo(0, 0);
      };
    },
    updateRemountKey() {
      this.remountKey = +new Date();
    },
    resetFilter() {
      const { routerToReset } = this.getModalState(MODAL_KEY_CATEGORIES_FILTER);
      this.$router.push(routerToReset);
      this.updateRemountKey();
    },
  },
  computed: {
    ...mapGetters(["getModalVisibilityState", "getModalState", "counts"]),
    isShowFilterModal() {
      return this.getModalVisibilityState(MODAL_KEY_CATEGORIES_FILTER);
    },
    attributes() {
      return range(this.levels).map(n => `${this.attribute}.lvl${n}`);
    },
    name() {
      return get(this.store, "name", "");
    },
    id() {
      return get(this.store, "entity_id", "");
    },
    filterMyProducts() {
      return !this.myProductIDs.length || !this.isMyProductsFiltered
        ? ""
        : ` AND (${this.myProductIDs
            .map(id => `objectID: ${id}`)
            .join(" OR ")})`;
    },
    filters() {
      return `${this.baseFilter}${
        this.isMyProductsFiltered ? `${this.filterMyProducts}` : ""
      }`;
    },
  },
  beforeDestroy() {
    this.setCounts(null);
  },
  watch: {
    // $route(to, from) {
    //   this.remountKey = +new Date();
    // },
    isShowFilterModal(isShow) {
      // manipulating directly to the dom to prevent rerender hierarchical menu, lead to the wrong selected option
      if (this.$refs.categoryFilterModalWrapperRef.style) {
        this.$refs.categoryFilterModalWrapperRef.style.visibility = isShow
          ? "visible"
          : "hidden";
      }
    },
  },
};
</script>

