<template>
  <!-- Extend the instantsearch hierarchicalMenu to provide multi-level category navigation based on design document -->
  <div>
    <!-- (isInner && !first.isRefined) || isDeadEnd, -->
    <div
      :class="[
        level !== 0 && 'category-list__inner',
        `category-list--lv-${level}`,
      ]"
    >
      <ul v-for="item in items" :key="item.value">
        <li
          :class="[
            'category-filter__item',
            item.isRefined && 'category-filter__item--refined',
          ]"
        >
          <i class="icon--arrow-left" v-if="item.isRefined" />
          <router-link
            :to="createURLForRouterLink(item.value)"
            :event="''"
            @click.native="
              handleLinkClick({
                value: item.value,
                to: createURLForRouterLink(item.value),
              })
            "
            >{{ item.label }}</router-link
          >
          <ais-refinement-list
            class="category-filter__refinement"
            v-if="item.isRefined && !hasData(item)"
            attribute="brand"
          />
          <ais-hierarchical-menu-list
            v-if="hasData(item)"
            :items="[...item.data]"
            :refine="refine"
            :createURL="createURL"
            :level="level + 1"
            :name="name"
            :basePath="basePath"
          />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import itemsCountMixin from "@/mixins/itemsCountMixin";
const pipe = (...fns) => x => fns.reduce((y, f) => f(y), x);

const splitValue = item => item.value.split(" > ");
const joinValue = arr => arr.join(" > ");
const nthLast = n => arr => arr[arr.length - n] || "";
const removeLast = (n = 1) => arr => {
  const ret = [...arr];
  ret.splice(-n, n);
  return ret;
};

export default {
  name: "AisHierarchicalMenuList",
  mixins: [itemsCountMixin],
  props: {
    level: { type: Number, default: 0 },
    items: Array,
    refine: Function,
    createURL: Function,
    name: String,
    basePath: String,
    isRoot: Boolean,
  },
  methods: {
    ...mapActions(["toggleNoResetModal"]),
    handleLinkClick({ value, to }) {
      this.toggleNoResetModal(true);
      this.refine(value);
      this.$router.push(to);
    },
    hasData(item) {
      return item.data && item.data.length;
    },
    canShow(item) {
      return !item.isRefined && !this.first.isRefined;
    },
    createURLForRouterLink(value) {
      const url = this.createURL(value);
      const { basePath } = this;

      const indexWithVerslun = url.indexOf(`/verslun/${basePath}`);
      return (
        url &&
        url.slice(
          indexWithVerslun !== -1
            ? indexWithVerslun
            : url.indexOf(`/${basePath}`)
        )
      );
    },
    createRootURL(value) {
      const { basePath, createURL } = this;
      const url = createURL(value);
      if (!url || !basePath) return url;

      return "/verslun/" + basePath;
    },
  },
  computed: {
    isChildRefined() {
      const { first, hasData } = this;
      return first.isRefined && hasData(first) && first.data[0].isRefined;
    },
    isDeadEnd() {
      const { first, hasData } = this;
      return first.isRefined && !hasData(first);
    },
    isInner() {
      return !!this.level;
    },
    first() {
      return this.items[0];
    },
    category() {
      const { level, name, first } = this;
      const label = !level
        ? name
        : pipe(
            splitValue,
            nthLast(2)
          )(first);
      const value = pipe(
        splitValue,
        removeLast(),
        joinValue
      )(first);
      return { label, value };
    },
  },
};
</script>

