export default {
  name: "itemsCountMixin",
  methods: {
    showCategory() {
      this.$store.dispatch("setCounts", this.first.count);
      return this.first.value;
    },
    showSubCategory() {
      if (this.level === 0) {
        let totalCount = this.items.reduce(
          (total, cur) => total + cur.count,
          0
        );
        this.$store.dispatch("setCounts", totalCount);
        return;
      }
      this.isDeadEnd && this.$store.dispatch("setCounts", this.first.count);
      return this.first.value;
    },
  },
};
